const responseErrorMessage: {[key: number]: string} = {
  '400': '',
  '401': '',
  '404': '',
  '405': '',
  '415': '',
  '500': ''
}

export const ResponseError = (code: number): string => {
  return responseErrorMessage[code] || ''
}
